@import url(https://fonts.googleapis.com/css?family=Raleway:200);
@import url(https://fonts.googleapis.com/css?family=Prata);
@import url(https://fonts.googleapis.com/css?family=Roboto+Mono);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*, body{
  margin:0;
  color:#47525D;
  outline: none;
}
.body-container{
  margin: 20px;
}

button,
button::after {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

button {
  background: none;
  border: 3px solid red;
  border-radius: 5px;
  color: red;
  display: block;
  font-size: 14px;
  margin: 10px;
  padding: 10px;
  position: relative;
  cursor:pointer;
}

button::before,
button::after {
  background: red;
  content: '';
  position: absolute;
  z-index: -1;
}

button:hover {
  color: white;
}
.change-btn{
  position: fixed;
  bottom: 30px;
  right: 30px;
  z-index: 999;
  background: white;
  border-radius: 2px;
}

.change-btn::after {
  height: 100%;
  left: 0;
  top: 0;
  width: 0;
}

.change-btn:hover:after {
  width: 100%;
}

.self-intro{
  font-size: 80px;
  font-family: 'Raleway', sans-serif;
  width: 320px;
  line-height: 80px;
}
.edu-container{
  margin-top:60vh;
  text-align: right;
  padding-right:20px;
}
.emp-container{
  margin-top:40vh;
  text-align: right;
  padding-right:20px;
}
.skill-container{
  margin-top:20vh;
  text-align: left;
  padding-left:20px;
}
.sub-title{
  font-size: 28px;
  font-family: 'Prata', serif;
  border-bottom: 1px solid #47525D;
}
.sub-desc-title{
  font-size: 18px;
  font-weight: 600;
  font-family: 'Roboto Mono', monospace;
  padding-top: 20px;
}
.sub-desc-skill{
  font-size: 16px;
  font-family: 'Roboto Mono', monospace;
  text-decoration: underline;
}
.sub-desc{
  font-size: 16px;
  font-family: 'Roboto Mono', monospace;
}

.container{
  display: flex;
  position:relative;
  margin-left: 20px;
  -webkit-perspective:999px;
          perspective:999px;
  width:500px
}
.card-slow{
  width:65px;
  height:80px;
  -webkit-transform-style:preserve-3d;
          transform-style:preserve-3d;
  -webkit-transition: -webkit-transform 1.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: -webkit-transform 1.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 1.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 1.5s cubic-bezier(0.175, 0.885, 0.32, 1.275), -webkit-transform 1.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.card{
  width:65px;
  height:80px;
  -webkit-transform-style:preserve-3d;
          transform-style:preserve-3d;
  -webkit-transition: -webkit-transform 1.0s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: -webkit-transform 1.0s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 1.0s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 1.0s cubic-bezier(0.175, 0.885, 0.32, 1.275), -webkit-transform 1.0s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}


.card div{
  position:absolute;
  width:100%;
  height:100%;
  -webkit-backface-visibility:hidden;
          backface-visibility:hidden;
  background:#fff;
  display:flex;
  justify-content:center;
  align-items:center;
  color:#47525D
}

.card-slow div{
  position:absolute;
  width:100%;
  height:100%;
  -webkit-backface-visibility:hidden;
          backface-visibility:hidden;
  background:#fff;
  display:flex;
  justify-content:center;
  align-items:center;
  color:#47525D
}

.back{
  -webkit-transform:rotateY(180deg);
          transform:rotateY(180deg)
}

.flipped{
  -webkit-transform:rotateY(180deg);
          transform:rotateY(180deg)
}
.circle {
  -webkit-animation: bounce 1.8s ease-in-out infinite;
          animation: bounce 1.8s ease-in-out infinite;
  position: fixed;
  border: 1px solid #47525D;
  background-color: #FFFFFF;
  height: 20px;
  border-radius:50%;
  width: 20px;
  top: 90vh;
  margin-left: -10px;
}
@-webkit-keyframes bounce {
    0%, 20%, 50%, 80%, 100% {-webkit-transform: translateY(0);transform: translateY(0);}
    40% {-webkit-transform: translateY(-30px);transform: translateY(-30px);}
    60% {-webkit-transform: translateY(-20px);transform: translateY(-20px);}
}
@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {-webkit-transform: translateY(0);transform: translateY(0);}
    40% {-webkit-transform: translateY(-30px);transform: translateY(-30px);}
    60% {-webkit-transform: translateY(-20px);transform: translateY(-20px);}
}

